import { Box, Button, Grid, Paper, Typography } from "@mui/material";
import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import LinearProgress from '@mui/material/LinearProgress';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import FactoryIcon from '@mui/icons-material/Factory';
import HouseIcon from '@mui/icons-material/House';
import LocalPostOfficeIcon from '@mui/icons-material/LocalPostOffice';

const Tracking = () => {
    const location = useLocation();
    const { product_name, thumbnail, price,date } = location.state;
    const navigate = useNavigate(); 
    const [progress, setProgress] = React.useState(0);
    const [buffer, setBuffer] = React.useState(10);

    const progressRef = React.useRef(() => { });
    React.useEffect(() => {
        progressRef.current = () => {
            if (progress > 100) {
                setProgress(0);
                setBuffer(10);
            } else {
                const diff = Math.random() * 10;
                const diff2 = Math.random() * 10;
                setProgress(progress + diff);
                setBuffer(progress + diff + diff2);
            }
        };
    });

    React.useEffect(() => {
        const timer = setInterval(() => {
            progressRef.current();
        }, 500);

        return () => {
            clearInterval(timer);
        };
    }, []);

    return (
        <Box>
            <Grid container justifyContent='center' alignItems='center' style={{ minHeight: '100vh' }} spacing={2}>
                <Grid item xs={12} md={6} lg={3}>
                    <Paper sx={{ backgroundColor: "#55aaee", color: "white", padding: 2 }} elevation={12}>
                        <Grid container spacing={2} alignItems="center">
                            <Grid item xs={12}>
                                <img src={thumbnail} alt="Product Thumbnail" width="100%" />
                            </Grid>
                            <Grid item xs={12}>
                                <Typography>{product_name}</Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <Typography>${price}</Typography>
                            </Grid>
                            <Grid item xs={12}>
                                <Typography>Order Date : {date}</Typography>
                            </Grid>
                        </Grid>
                    </Paper>
                </Grid>
                <Grid item xs={12} md={6}>
                    <Paper sx={{ backgroundColor: "#f5f5f5", padding: 2 }} elevation={2}>
                        <Grid container justifyContent='center' alignItems='center' spacing={2}>
                            <Grid item xs={12}>
                                <Typography variant="h6">Order Status: Working on Your Order</Typography>
                                <Box sx={{ width: '100%', marginTop: 2 }}>
                                    <LinearProgress variant="buffer" value={progress} valueBuffer={buffer} />
                                </Box>
                            </Grid>
                            <Grid item xs={6} sm={3} textAlign="center">
                                <FactoryIcon />
                                <Typography>Processing</Typography>
                            </Grid>
                            <Grid item xs={6} sm={3} textAlign="center">
                                <LocalPostOfficeIcon />
                                <Typography>Shipped</Typography>
                            </Grid>
                            <Grid item xs={6} sm={3} textAlign="center">
                                <LocalShippingIcon />
                                <Typography>Transit</Typography>
                            </Grid>
                            <Grid item xs={6} sm={3} textAlign="center">
                                <HouseIcon />
                                <Typography>Delivered</Typography>
                            </Grid>
                        </Grid>
                    </Paper>
                    <Button variant='contained' onClick={() => navigate(-1)} sx={{ marginTop: 2 }}>
                        Go Back
                    </Button>
                </Grid>
            </Grid>
        </Box>
    );
}

export default Tracking;
