import React from "react";
import { useLocation } from 'react-router-dom';
import { Box, Button, Grid, Paper, Typography } from "@mui/material";
import DoubleArrowIcon from '@mui/icons-material/DoubleArrow';
import { useNavigate } from "react-router-dom";
import { cart, orders, db } from "../LokiDB";

const OrderSuccess = () => {
    const location = useLocation();
    const { cartItems, username, name, dp, address, country } = location.state;
    const navigate = useNavigate();

    const BillingPage = () => {
        // Push to orders here
        const currdate = new Date().toLocaleDateString();
        const newOrder = {
            email: username,
            products: cartItems,
            orderDate: currdate // Call the function to get the formatted date
        };
        orders.insert(newOrder);
        console.log("New Order:", newOrder);

        const cartCollection = db.getCollection("cart"); // Replace "cart" with your actual collection name
        // cart deletion was here

        navigate('/billing', {
            state: { cartItems: cartItems, username: username, name: name, dp: dp, address: address, country: country }
        });
    }

    const totalAmount = cartItems.reduce((total, item) => total + item.price, 0);

    return (
        <Box>
            <Grid container justifyContent='center' alignItems='center' style={{ minHeight: '100vh' }}>
                <Grid item xs={12} textAlign='center'>
                    <Typography variant="h4" sx={{ color: "green" }}>Products Added!</Typography>
                </Grid>
                <Grid container spacing={2} alignItems="center">
                    <Grid item xs={12} textAlign='center'>
                        <Typography variant="h5">{name}</Typography>
                    </Grid>
                    <Grid item xs={12} textAlign='center'>
                        <Paper sx={{ backgroundColor: "#9ec5de", padding: 2 }}>
                            {cartItems.map((order, index) => (
                                <Grid item xs={12} key={index}>
                                    <Paper elevation={3} sx={{ padding: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 2 }}>
                                        <Typography variant="body1">{order.title}</Typography>
                                        <Typography variant="body1">${order.price}</Typography>
                                    </Paper>
                                </Grid>
                            ))}
                            <Grid item xs={12} textAlign='center'>
                                <Typography variant="h5">TOTAL AMOUNT: ${totalAmount}</Typography>
                            </Grid>
                            <Grid item xs={12} textAlign='center'>
                                <Button variant="contained" sx={{ backgroundColor: "green" }} onClick={BillingPage}>
                                    PROCEED TO PAYMENT <DoubleArrowIcon />
                                </Button>
                            </Grid>
                        </Paper>
                    </Grid>
                </Grid>
            </Grid>
        </Box>
    );
}

export default OrderSuccess;
