import React, { useEffect, useState, useContext, createContext } from 'react';
import { getAllCategories, getAllProducts } from '../service';
import Products from './Products';
import AppBar from './Appbar';
import TopAppBar from './Appbar';
import '../App.css'
import Filter from './Filter';
import Categories from './Categories';
import Footer from './Footer';
import { useLocation } from 'react-router-dom';
import { Button, Grid } from '@mui/material';
import FilterAltIcon from '@mui/icons-material/FilterAlt';

export const ProductContext = createContext();

function App() {
  const location = useLocation();
  const { username, country, pincode, address, dp, name } = location.state;

  const [productlist, setProductList] = useState([]);
  const [categories, setCategories] = useState([]);
  const [productpagelist, setProductPageList] = useState([]);



  // Carousel state
  const [currentSlide, setCurrentSlide] = useState(0);
  const carouselImages = [
    "https://cdn.cms-twdigitalassets.com/content/dam/marketing-twitter/apac/en_gb/success-stories/flipkart-bigbillionmuqabla-2021/title.jpg.twimg.768.jpg",
    "https://i.ytimg.com/vi/8326CoNyKMc/maxresdefault.jpg",
    "https://assets.thehansindia.com/h-upload/2019/09/19/217781-dhoni.webp",
    "https://i.ytimg.com/vi/VyK0jjMmojU/maxresdefault.jpg",
    "https://i.ytimg.com/vi/s1RSOfoeJm4/maxresdefault.jpg",
    "https://i.ytimg.com/vi/61eNbq9upKY/maxresdefault.jpg"
  ];



  useEffect(() => {
    //console.log('Received username:', username);
    const getProducts = async () => {
      const data = await getAllProducts();
      setProductList(data);
    }
    const getCategories = async () => {
      const data = await getAllCategories();
      setCategories(data);
    }

    getProducts();
    getCategories();
    

  }, [])


  const showAllProducts=(event)=>
  {
    
    const getProducts = async () => {
      const data = await getAllProducts();
      setProductList(data);
    }
    getProducts();

  }
   
  return (
    <div className="App">
      <ProductContext.Provider value={{ productlist, setProductList, categories, setCategories, productpagelist, setProductPageList, username, country, pincode, address, dp, name }}>
        <TopAppBar />
        <Grid container justifyContent='center' alignContent='center' alignItems='center' sx={{ marginTop: "50px", backgroundColor: "#3180e8" }} spacing={0}>
          <div className="carousel-container">
           <img src="https://cdn.cms-twdigitalassets.com/content/dam/marketing-twitter/apac/en_gb/success-stories/flipkart-bigbillionmuqabla-2021/title.jpg.twimg.768.jpg" alt="Carousel 1" />
           <img src="https://i.ytimg.com/vi/8326CoNyKMc/maxresdefault.jpg" alt="Carousel 2" />
           <img src="https://assets.thehansindia.com/h-upload/2019/09/19/217781-dhoni.webp" alt="Carousel 2" />
           <img src="https://i.ytimg.com/vi/VyK0jjMmojU/maxresdefault.jpg" /> 
           <img src="https://i.ytimg.com/vi/s1RSOfoeJm4/maxresdefault.jpg" />
           <img src="https://i.ytimg.com/vi/61eNbq9upKY/maxresdefault.jpg" />
           <img src="https://bestmediainfo.com/uploads/2021/09/Amazon_Flipkart_sales_10.jpg" />  
          </div>
        </Grid>
        
        <Categories />
        <Button variant='contained' color='primary' onClick={showAllProducts} sx={{padding:1,mt:"10px"}}><FilterAltIcon></FilterAltIcon>Show All Products</Button>
        <Products />
      </ProductContext.Provider>
    </div>
  );
}

export default App;
