import React from "react";
import { ProductContext } from "./App";

import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Category from "./Category";
import CategoryCard from "./CategoryCard";
import '../App.css'



const Categories=()=>
{


const { productlist, setProductList,categories,setCategories} = React.useContext(ProductContext);

   return(
    <Box marginLeft={2} marginRight={2} marginTop={5}>
        <Grid container spacing={1} justifyContent='center'  sx={{marginRight:12}}>
            <div className="category" style={{overflowX:"scroll", overflowY:"hidden",display:"flex",marginLeft:"200px", marginRight:"200px"}}>
            {categories.map((item) => (
                <Grid item xs={1.125} sx={{borderRadius:8}} m={1}  >
                    <Paper elevation={4} sx={{backgroundColor:"#ffffff", borderRadius: 2, color:"white"}}>
                <CategoryCard key={item}>
                    <Category 
                       ctgry={item}
                    />
                </CategoryCard>
                </Paper>
                </Grid>
                
            ))}
            </div>
            </Grid>
            </Box>
        
        
  
   )
}

export default Categories;


