import React, { useEffect } from "react";
import { Link, useLocation } from 'react-router-dom';
import AppBar from "./Appbar";
import { Box, Button, Grid, Paper, Typography } from "@mui/material";
import PersonIcon from '@mui/icons-material/Person';
import MailIcon from '@mui/icons-material/Mail';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import { cart, orders } from "../LokiDB";
import { useNavigate } from 'react-router-dom';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import ShoppingBasketIcon from '@mui/icons-material/ShoppingBasket';
import FlagCircleIcon from '@mui/icons-material/FlagCircle';

const Profile = () => {
    const navigate = useNavigate();
    const location = useLocation();
    const { username, country, address, dp, name } = location.state;
    const [cartlist, setCartList] = React.useState([]);
    const [orderlist, setOrderList] = React.useState([]);
    useEffect(() => {
        const cartItems = cart.find({ email: username });
        const orderItems = orders.find({ email: username });
        setCartList(cartItems);
        setOrderList(orderItems);
    }, []);

    const placeOrder = () => {
        navigate('/orders', {
            state: { cartItems: cartlist, orderItems: orderlist, username: username, name: name, dp: dp, address: address,country:country }
        });
    }

    return (
        <div style={{ backgroundImage: "url(https://wallpapers.com/images/hd/abstract-background-6m6cjbifu3zpfv84.jpg)", backgroundSize: "cover" }}>
            <AppBar />
            <Box sx={{ margin: "0 20px", marginTop: "100px", borderRadius: "15px" }}>
                <Grid container justifyContent='center' spacing={2} sx={{ padding: 2 }}>
                    <Grid item xs={12} md={6} > 
                        <Paper elevation={20} sx={{ padding: 2,backgroundColor:"white",color:"black" }} >
                            <Grid container justifyContent='center' alignContent='center' alignItems='center' spacing={2}>
                                <Grid item xs={12}>
                                    <div style={{ width: "150px", height: "150px", borderRadius: "100px", overflow: "hidden", display: "flex", alignItems: "center", justifyContent: "center", backgroundColor: "#ccc" }}>
                                        {dp ? <img src={dp} style={{ width: "150px", height: "150px" }} alt="Profile" /> : <PersonIcon style={{ fontSize: 80, color: "#666" }} />}
                                    </div>
                                </Grid>
                                <Grid item xs={12} justifyContent='center'>
                                    <Typography variant="h5">{name}</Typography>
                                </Grid>
                                <Grid item xs={12} alignItems='center' display='flex'>
                                    <LocationOnIcon sx={{ marginRight: 1 }} />
                                    <Typography> {address}</Typography>
                                </Grid>
                                <Grid item xs={12} alignItems='center' display='flex'>
                                    <MailIcon sx={{ marginRight: 1 }} />
                                    <Typography>{username}</Typography>
                                </Grid>
                                <Grid item xs={12} alignItems='center' display='flex'>
                                    <FlagCircleIcon sx={{ marginRight: 1 }} />
                                    <Typography> {country}</Typography>
                                </Grid>
                            </Grid>
                        </Paper>
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Paper elevation={12} sx={{ padding: 2, maxHeight: "33vh", overflow: "auto" }}>
                            <Grid item xs={12}><ShoppingCartIcon /><Typography variant="h6">Cart</Typography></Grid>
                            {cartlist.map((order, index) => (
                                <Grid container alignItems="center" spacing={2} key={index} padding={1}>
                                    <Grid item xs={12}>
                                        <Paper sx={{ backgroundColor: "#48a3db", display: "flex", alignItems: "center", padding: 2, color: "white" }} elevation={5}>
                                            <Grid item xs={4}>
                                                <img src={order.thumbnail} alt={order.title} width={"100px"} height={"50px"} style={{ marginRight: "10px" }} />
                                            </Grid>
                                            <Grid item xs={8}>
                                                <Typography variant="body2">{order.title}</Typography>
                                                <Typography variant="body2">${order.price}</Typography>
                                            </Grid>
                                        </Paper>
                                    </Grid>
                                </Grid>
                            ))}
                        </Paper>
                        <Paper elevation={12} sx={{ marginTop: 2, padding: 2, maxHeight: "33vh", overflow: "auto" }}>
                            <Grid item xs={12}><Typography variant="h6">Orders</Typography></Grid>
                            {orderlist.map((order, orderIndex) => (
                                <React.Fragment key={orderIndex}>
                                    {order.products.map((product, productIndex) => (
                                        <Grid item xs={12} key={productIndex}>
                                            <Link to={'/tracking'} state={{ username, dp, name, address, country, thumbnail: product.thumbnail, product_name: product.title, price: product.price,date:order.orderDate }} style={{ textDecoration: 'none', color: 'inherit' }}>
                                                <Paper elevation={10} sx={{ backgroundColor: "#48a3db", color: "white", display: "flex", flexDirection: "column", alignItems: "center", padding: 2, marginBottom: 2 }}>
                                                    <img src={product.thumbnail} alt={product.title} width={"100px"} height={"50px"} />
                                                    <Typography variant="body2">{product.title}</Typography>
                                                    <Typography variant="body2">${product.price}</Typography>
                                                    <Typography variant="body2">Order Date: {order.orderDate}</Typography>
                                                </Paper>
                                            </Link>
                                        </Grid>
                                    ))}
                                </React.Fragment>
                            ))}
                        </Paper>
                    </Grid>
                    <Grid item xs={12} justifyContent='center' display='flex'>
                        <Button variant="contained" onClick={placeOrder} color="warning">BUY NOW</Button>
                    </Grid>
                    
                </Grid>
            </Box>
        </div>
    )
}

export default Profile;
