import { Grid, Paper, Typography } from "@mui/material";
import React from "react";
import Button from '@mui/material/Button';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import Rating from '@mui/material/Rating';
const Product = (props) => {
    //const [value, setValue] = React.useState(2);
    const { id, title,  price,  images, rating, discount } = props;
    return (

        <Grid container justifyContent='center' alignContent='center' alignItems='center'>

            <Grid item xs={12} justifyContent='center'>
                <img src={images[2]} width={"200px"} height={"150px"} style={{ marginTop: "10px" }} />
            </Grid>
            <Grid item xs={12} sx={{ padding: 3 }}>
                <Typography noWrap variant="h5">{title}</Typography>

            </Grid>

            <Grid item xs={12}>
                <Typography noWrap variant="h5" sx={{color:"green"}}>${Math.round(price - (price * discount / 100))}</Typography>
            </Grid>


            <Grid item xs={12}>
                <Typography variant="h7" sx={{textDecoration: 'line-through'}}>${price}</Typography>
            </Grid>



            <Grid item xs={12}>
                <Typography sx={{ color: "red" }}>{discount}% OFF!! </Typography>
            </Grid>
            <Grid item>
                <Rating name="read-only" value={rating} readOnly />
            </Grid>
            <Grid item xs={12} sx={{ padding: 1 }}>
                <Button variant="contained"><ShoppingCartIcon></ShoppingCartIcon>BUY NOW</Button>
            </Grid>


        </Grid>


    )
}

export default Product;

