import * as React from 'react';
import CssBaseline from '@mui/material/CssBaseline';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Toolbar from '@mui/material/Toolbar';
import Paper from '@mui/material/Paper';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import AddressForm from './AddressForm';
import PaymentForm from './PaymentForm';
import Review from './Review';
import orderId from 'order-id-generator';
import { Grid } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { cart } from '../LokiDB';

function Copyright() {
   
  return (
    <Typography variant="body2" color="text.secondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://mui.com/">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

function makeid(length) {
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    let counter = 0;
    while (counter < length) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
      counter += 1;
    }
    return result;
}

const steps = ['Shipping address', 'Payment details', 'Review your order'];



export default function Checkout(props) {
  const navigate=useNavigate();
    const{name,username,dp,address,country}=props;
    const uid = makeid(28);
    //console.log('UID IS ',uid);
  const [activeStep, setActiveStep] = React.useState(0);

  const handleNext = () => {
    setActiveStep(activeStep + 1);
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };


  function getStepContent(step) {
    switch (step) {
      case 0:
        return <AddressForm />;
      case 1:
        return <PaymentForm />;
      case 2:
        return <Review name={name} username={username} dp={dp} address={address} country={country}/>;
      default:
        throw new Error('Unknown step');
    }
  }


  const GoHome=()=>
  {
    // const cartItems = cart.find({ email: username });
    cart.chain().find({ email: username }).remove();
    navigate(`/home`, {
      state: { username, name,dp,address,country }
  });
  }

  return (
    <React.Fragment>
      <CssBaseline />
      <AppBar
        position="absolute"
        color="primary"
        elevation={0}
        sx={{
          position: 'relative',
          borderBottom: (t) => `1px solid ${t.palette.divider}`,
          
        }}
      >
        <Toolbar>
          <Typography variant="h6" color="inherit" noWrap>
            Flipkart 2.0
          </Typography>
        </Toolbar>
      </AppBar>
      <Container component="main" maxWidth="sm" sx={{ mb: 4 }}>
        <Paper variant="outlined" sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}>
          <Typography component="h1" variant="h4" align="center">
            Checkout
          </Typography>
          <Stepper activeStep={activeStep} sx={{ pt: 3, pb: 5 }}>
            {steps.map((label) => (
              <Step key={label}>
                <StepLabel>{label}</StepLabel>
              </Step>
            ))}
          </Stepper>
          {activeStep === steps.length ? (

            <Grid container justifyContent='center'>
                <Grid item>
                <Grid item xs={12} textAlign='center'>
                <img src="https://i.gifer.com/7efs.gif" width={"350px"} height={"250px"} alt="Checkmark" />
            </Grid>
                </Grid>
                <Grid item>
            <React.Fragment>
                <Grid container>
                    <Grid item>
                    </Grid>
                    <Grid item xs={12}>
                    <Typography variant="h5" gutterBottom>
                Thank you for your order.
              </Typography>
              <Typography variant="subtitle1">
                Your order number is <Typography variant="p" sx={{color:"red"}}>{uid.toUpperCase()}</Typography>. We have emailed your order
                confirmation, and will send you an update when your order has
                shipped.
              </Typography>
              </Grid>
              <Grid item>
                <Button variant='contained' color='success' onClick={GoHome}>GO TO HomePage</Button>
              </Grid>
                    </Grid>
              
            </React.Fragment>
            </Grid>
            </Grid>
          ) : (
            <React.Fragment>
              {getStepContent(activeStep)}
              <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
                {activeStep !== 0 && (
                  <Button onClick={handleBack} sx={{ mt: 3, ml: 1 }}>
                    Back
                  </Button>
                )}

                <Button
                  variant="contained"
                  onClick={handleNext}
                  sx={{ mt: 3, ml: 1 }}
                >
                  {activeStep === steps.length - 1 ? 'Place order' : 'Next'}
                </Button>
              </Box>
            </React.Fragment>
          )}
        </Paper>
        <Copyright />
      </Container>
    </React.Fragment>
  );
}