import { Grid, Paper, Typography } from "@mui/material";
import React from "react";
import { ProductContext } from "./App";
import { getByCategories } from "../service";
const Category=(props)=>

{
    const{ctgry}=props;
    //console.log('ITEM IS ',ctgry);
    const { productlist, setProductList } = React.useContext(ProductContext);

    const filterContent=(event)=>
    {
        event.preventDefault();
        console.log(ctgry,'Clicked');
        const getbycategory=async ()=>
        {
           const data=await getByCategories(ctgry);
           setProductList(data);

        }
        getbycategory();
    }

   return(
    <div onClick={filterContent} style={{borderRadius:8}}>
        <Grid container justifyContent='center' alignContent='center' alignItems='center'>
            
        
            <Grid item xs={12}  sx={{padding:3,":hover":{transform:"scale(1.2)"}}}>
             
            <Typography noWrap variant="p" sx={{fontSize:12}}>{ctgry.charAt(0).toUpperCase() + ctgry.slice(1).toLowerCase()}</Typography>
          
            </Grid>
            
            
        </Grid>
        </div>
   
   )
}

export default Category;

