import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { getByID } from "../service";
import { Grid, Paper, Typography, Avatar } from "@mui/material";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import BoltIcon from "@mui/icons-material/Bolt";
import AppBar from "./Appbar";
import TextField from "@mui/material/TextField";
import { reviews, cart } from "../LokiDB";
import { useLocation } from 'react-router-dom';
import { ProductContext } from "./App";
import TopAppBar from "./Appbar";
import Rating from '@mui/material/Rating';
import { useNavigate } from 'react-router-dom';

const ProductPage = () => {
    const navigate = useNavigate();
    const location = useLocation();
    const { productId } = useParams();
    const { username, dp, name, address, country } = location.state || {};
    const [productrating, setRating] = useState();
    const [reviewlist, setReviewsList] = useState([]);
    const [reviewtxt, setReviewText] = useState("");
    const [productinfo, setProductInfo] = useState({
        id: "",
        title: "",
        description: "",
        brand: "",
        category: "",
        rating: "",
        stock: "",
        images: [],
        thumbnail: "",
        price: "",
        discount: ''
    });

    useEffect(() => {
        const getProduct = async () => {
            const data = await getByID(productId);
            setProductInfo(data);
        };

        getProduct();
        const productReviews = reviews.findOne({ id: productId });
        if (productReviews) {
            setReviewsList(productReviews.reviews);
        } else {
            setReviewsList([]);
        }
    }, [productId]);

    const handleChange = (event) => {
        setReviewText(event.target.value);
    };

    // const submitReview = () => {
    //     const productReview = reviews.findOne({ id: productId });
    //     if (productReview) {
    //         const updatedReviews = [...productReview.reviews, reviewtxt];
    //         reviews.insert({ id: productId, reviews: updatedReviews,rating:rating });
    //         setReviewsList(updatedReviews);
    //     } else {
    //         const newReview = { id: productId, reviews: [reviewtxt],rating:rating };
    //         reviews.insert(newReview);
    //         setReviewsList([reviewtxt]);
    //     }
    //     setReviewText("");
    // };

    const submitReview = (event) => {
        event.preventDefault();
        const productReview = reviews.findOne({ id: productId });
        if (productReview) {
            const updatedReviews = [
                ...productReview.reviews,
                { review: reviewtxt, rating: productrating } // Include both review text and rating
            ];
            reviews.insert({ id: productId, reviews: updatedReviews });
            setReviewsList(updatedReviews);
        } else {
            const newReview = {
                id: productId,
                reviews: [{ review: reviewtxt, rating: productrating }] // Include both review text and rating
            };
            reviews.insert(newReview);
            setReviewsList([reviewtxt]);
        }
        setReviewText("");
        setRating(0); // Reset the rating after submitting a review
    };


    const addToCart = () => {
        cart.insert({
            email: username,
            productid: productId,
            title: productinfo.title,
            thumbnail: productinfo.thumbnail,
            price: productinfo.price
        });
        navigate(`/profile/${username}`, {
            state: { username, dp, name, address, country }
        });
    };

    return (
        <div>
            <TopAppBar />
            <Box
                sx={{
                    marginTop: "100px",
                    marginInlineStart: "auto",
                    marginInlineEnd: "auto",
                    backgroundColor: "#bedce8",
                    padding: 5,
                }}
            >
                <Grid container justifyContent="center">
                    <Grid item xs={12} md={6}>
                        <img src={productinfo.images[1]} width={"100%"} alt="Product" />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Box sx={{ padding: 2 }}>
                            <Typography variant="h4">{productinfo.title}</Typography>
                            <Rating name="read-only" value={productinfo.rating} readOnly />
                            <Typography variant="h5">${productinfo.price - Math.round(productinfo.price * productinfo.discountPercentage / 100)}</Typography>
                            <Typography sx={{ color: "green" }} variant="h6">{productinfo.discountPercentage} % OFF !!</Typography>
                            <Typography>Seller: {productinfo.brand}</Typography>
                            <Typography sx={{ color: "red" }}>Hurry! Only {productinfo.stock} left!!</Typography>
                            <Typography variant="body1">{productinfo.description}</Typography>
                            <Button variant="contained" color="warning" onClick={addToCart} sx={{ marginTop: 2 }}>
                                <ShoppingCartIcon /> Add To Cart
                            </Button>
                        </Box>
                    </Grid>
                </Grid>
            </Box>
            <Box sx={{ marginTop: "50px", marginLeft: "auto", marginRight: "auto", padding: 5 }}>
                <Typography variant="h5">Reviews</Typography>
                <Grid container spacing={2} alignItems="center">
                    <Grid item xs={12} md={6}>
                        <TextField
                            id="standard-basic"
                            label="Post your review..."
                            variant="standard"
                            fullWidth
                            value={reviewtxt}
                            onChange={handleChange}
                        />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Rating
                            name="controlled"
                            value={productrating}
                            onChange={(newValue) => {
                                setRating(newValue.target.value);
                            }}
                        />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Button variant="contained" color="warning" onClick={submitReview} fullWidth>
                            <BoltIcon /> Post Review
                        </Button>
                    </Grid>
                    {reviewlist.map((reviewData, index) => (
                        <Grid item xs={12} key={index}>
                            <Paper elevation={6} sx={{ display: "flex", alignItems: "center", padding: 2, backgroundColor: "#48a3db", color: "white" }}>
                                <Avatar alt="Remy Sharp" src={dp} sx={{ marginRight: 2 }} />
                                <div style={{ display: "flex", flexDirection: "column", alignItems: "flex-start", flex: 1 }}>
                                    <Typography variant="h5">{name}</Typography>
                                    <Rating name="read-only" value={reviewData.rating} readOnly /> {/* Use reviewData.rating */}
                                    <Typography variant="p">{reviewData.review}</Typography>
                                </div>
                                <div style={{ marginLeft: "auto" }}>
                                    <Typography variant="body2">{new Date().toLocaleString()}</Typography>
                                </div>
                            </Paper>
                        </Grid>
                    ))}

                </Grid>
            </Box>
        </div>
    );
};

export default ProductPage;
