export const getAllProducts = async () => {

    try {
      const productdata = await fetch('https://dummyjson.com/products');
      const data = await productdata.json();
      return data.products;
    } catch (error) {
      console.log('Api ', error.message);
    }
  
  };
 
  export const getAllCategories = async () => {

    try {
      const productdata = await fetch('https://dummyjson.com/products/categories');
      const data = await productdata.json();
      //console.log('CATEGORIES:',data);
      return data;
    } catch (error) {
      console.log('Api ', error.message);
    }
  
  };

  export const getByCategories = async (category) => {

    try {
      const productdata = await fetch(`https://dummyjson.com/products/category/${category}`);
      const data = await productdata.json();
      //console.log('CATEGORIES:',data);
      return data.products;
    } catch (error) {
      console.log('Api ', error.message);
    }
  
  };

  export const getByID = async (id) => {

    try {
      const productdata = await fetch(`https://dummyjson.com/products/${id}`);
      const data = await productdata.json();
      //console.log('CATEGORIES:',data);
      return data;
    } catch (error) {
      console.log('Api ', error.message);
    }
  
  };






