import React, { useContext } from "react";
import { ProductContext } from "./App";
import Product from "./Product";
import Card from "./Card";
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import { Link, useNavigate } from "react-router-dom";

const Products = () => {
    const { productlist, username, dp, name, address, country } = useContext(ProductContext);
    const navigate = useNavigate();

    return (
        <Box marginLeft={2} marginRight={2} marginTop={5} marginBottom={10}>
            <Grid container justifyContent='center' spacing={6}>
                {productlist.map((item) => (
                    <Grid item xs={12} sm={6} md={4} lg={3} key={item.id}>
                        <Paper elevation={4} sx={{ backgroundColor: "#ffffff" }}>
                            <Link to={`/product/${item.id}`} state={{ username, dp, name, address, country }} style={{ textDecoration: 'none', color: 'inherit' }}>
                                <Card key={item.id}>
                                    <Product
                                        id={item.id}
                                        title={item.title}
                                        price={item.price}
                                        brand={item.brand}
                                        images={item.images}
                                        rating={item.rating}
                                        discount={item.discountPercentage}
                                    />
                                </Card>
                            </Link>
                        </Paper>
                    </Grid>
                ))}
            </Grid>
        </Box>
    )
}

export default Products;
