import { Grid } from "@mui/material";
import React from "react";


const CategoryCard=(props)=>
{
    return(
        <div>
            <Grid container sx={{backgroundColor:"#4d88ff", borderRadius: 2}}>
                <Grid item>
                {props.children}
                </Grid>
            </Grid>
         
        </div>
    )


}


export default CategoryCard;